import React from 'react';
import Wrapper from '../components/section-wrapper';
import { getData } from '../utils';

const { skills } = getData();

export default () => (
  <Wrapper className="skills-section">
    <h2 className="mb-4">Technical Skills</h2>
    <div className="skill-set" id="techincal">
      {skills.map(skill => (
        <div className="skill-set-container">
          <img src={skill.image} alt={skill.desc} className="skills-set-icon" />
          <div>{skill.desc}</div>
        </div>
      ))}
    </div>
  </Wrapper>
);
