import React, { useState } from 'react';
import Parser from 'html-react-parser';
import { Collapse } from 'reactstrap';
import { getData } from '../utils';
import { Wrapper } from '../components';

const { experience } = getData();
const Intro = () => {
  return (
    <Wrapper>
      <h2 className="mb-5" id="experience">
        {experience.heading}
      </h2>
      {experience.roles.map((role, index) => (
        <PositionItem key={`${role}`} position={role} index={index} />
      ))}
    </Wrapper>
  );
};

const PositionItem = ({ position, index }) => {
  const [isOpen, toggle] = useState(index <= 1);
  return (
    <>
      <div className="resume-item d-flex flex-column flex-md-row flex-sm-row">
        <div className="resume-content mr-auto">
          <div
            className="clickable-header"
            onClick={() => toggle(!isOpen)}
            onKeyPress={() => toggle(!isOpen)}
            role="button"
            tabIndex={0}
          >
            <h3 className="mb-0">{position.title}</h3>
            <img
              className={`caret ${isOpen ? '' : 'caret-close'}`}
              src="img/triangle.svg"
              alt="images-caret"
            />
          </div>
          <div className={`subheading ${isOpen ? 'mb-2' : ''}`}>
            <a
              href={position.company.href}
              target="_blank"
              rel="noopener noreferrer"
            >
              {position.company.name}
            </a>
            <span className="timespan">{position.timespan}</span>
          </div>
          <Collapse isOpen={isOpen}>
            <div className="description">
              <ul>
                {position.description.map(desc => (
                  <li key={desc}>{Parser(desc)}</li>
                ))}
              </ul>
            </div>
          </Collapse>
        </div>
        <div className="resume-date text-md-right">
          <span className="text-primary">{}</span>
        </div>
      </div>
      <div className="mb-5">
        <div className="subheading">Tech stack</div>
        <div className="sub-desc">
          {position.techStack.map(techStackDesc => (
            <div key={techStackDesc}>{techStackDesc}</div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Intro;
